body, h2, p{
  margin:0;
  padding: 0;
}

body{
  background-color: blueviolet;
  padding: 3em;
}

header img{
  width: 14%;
}

main{
  margin-top: 2em;
  display: flex;
  justify-content: space-between;
}

main img{
  width: 40%;
}

.content{
  width: 40%;
  padding-top:2em;
}

.content h2{
  font-size: 40px;
  color: white;
}

.content p{
  margin: 2em 0em;
  font-size: 15px;
  color: white;
}

main button{
  padding: 1em;
  border: none;
  border-radius: 30px;
  color: purple;
  width: 40%;
}

footer{
  float: right;
}

footer .fab{
  color: white;
  font-size: 15px;
  border: 1px solid white;
  padding: 0.5em;
  border-radius: 50%;
  margin-right: 5px;
}

footer .fab:hover{
  border-color: purple;
  color: purple;
}